import React from 'react';
import './canvasScale.css';
import PropTypes from 'prop-types';

function CanvasScale({ onChange, options, value }) {
  return (
    <div className="canvas-size">
      {options.map(num => (
        <div className="canvas-size__item" key={num}>
          <input
            checked={value === num}
            id={`canvas-size__item${num}`}
            name="canvas-size__item"
            onChange={onChange}
            type="radio"
            value={num}
          />
          <label htmlFor={`canvas-size__item${num}`}>{num}*{num}</label>
        </div>
      ))}
    </div>
  );
}

CanvasScale.propTypes = {
  onChange: PropTypes.func,
  options: PropTypes.arrayOf(
    PropTypes.number,
  ),
  value: PropTypes.number,
};

export default CanvasScale;
