export default (x1, y1, x2, y2, cb) => {
  let x0 = x1;
  let y0 = y1;

  if (x1 < x2 && y1 < y2) {
    while (x0 < x2) {
      cb(x0, y1);
      x0 += 1;
    }
    while (y0 < y2) {
      cb(x0, y0);
      y0 += 1;
    }
    while (x0 > x1) {
      cb(x0, y0);
      x0 -= 1;
    }
    while (y0 > y1) {
      cb(x0, y0);
      y0 -= 1;
    }
  }

  if (x1 < x2 && y1 > y2) {
    while (x0 < x2) {
      cb(x0, y1);
      x0 += 1;
    }
    while (y0 > y2) {
      cb(x0, y0);
      y0 -= 1;
    }
    while (x0 > x1) {
      cb(x0, y0);
      x0 -= 1;
    }
    while (y0 < y1) {
      cb(x0, y0);
      y0 += 1;
    }
  }

  if (x1 > x2 && y1 < y2) {
    while (x0 > x2) {
      cb(x0, y0);
      x0 -= 1;
    }
    while (y0 <= y2) {
      cb(x0, y0);
      y0 += 1;
    }
    while (x0 < x1) {
      cb(x0, y2);
      x0 += 1;
    }
    while (y0 > y1) {
      cb(x0, y0);
      y0 -= 1;
    }
  }

  if (x1 > x2 && y1 > y2) {
    while (x0 > x2) {
      cb(x0, y0);
      x0 -= 1;
    }
    while (y0 > y2) {
      cb(x0, y0);
      y0 -= 1;
    }
    while (x0 < x1) {
      cb(x0, y2);
      x0 += 1;
    }
    while (y0 < y1) {
      cb(x0, y0);
      y0 += 1;
    }
  }
};
