import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './fps.css';

function FPS({ onChange }) {
  const [framesPerSecond, setFramesPerSecond] = useState(1);
  return (
    <div className="FPS-wrapper">
      <span>{framesPerSecond} FPS</span>
      <input type="range" name="speed" min={1} max={24} defaultValue={1} onChange={ (e) => {
        setFramesPerSecond(+e.target.value);
        onChange(framesPerSecond);
      }} />
    </div>
  );
}

FPS.propTypes = {
  onChange: PropTypes.func,
};

export default FPS;
