import {
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import { even, odd } from '../../utils';

function PixelPen({ onMouseUp, ...props }) {
  const canvas = useContext(CanvasContext);
  const isMouseDown = useRef(false);
  const getImgLink = useCallback(() => {
    if (isMouseDown.current) {
      onMouseUp(canvas.toDataURL());

      isMouseDown.current = !isMouseDown.current;
    }
  }, [onMouseUp, canvas]);

  const handler = useCallback(() => {
    const ctx = canvas.getContext('2d');

    if (isMouseDown.current) {
      const rectsNum = props.penSize / 20;
      const innerFirstRectColumn = props.x / 20 + 1;
      const innerFirstRectRow = props.y / 20 + 1;

      for (let i = 0; i < rectsNum; i += 1) {
        for (let j = 0; j < rectsNum; j += 1) {
          const rowNum = innerFirstRectRow + i;
          const colNum = innerFirstRectColumn + j;

          if (
            (odd(rowNum) && odd(colNum))
            || (even(rowNum) && even(colNum))
          ) {
            ctx.fillStyle = props.secondaryColor;
          } else {
            ctx.fillStyle = props.primaryColor;
          }

          ctx.fillRect(props.x + (i * 20), props.y + (j * 20), 20, 20);
        }
      }
    } else {
      ctx.beginPath();
    }
  }, [canvas, props.penSize, props.primaryColor, props.secondaryColor, props.x, props.y]);

  const setMouseDown = useCallback((e) => {
    isMouseDown.current = !isMouseDown.current;
    handler(e);
  }, [handler]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', setMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', setMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, setMouseDown]);

  return null;
}

PixelPen.propTypes = {
  x: PropTypes.number,
  y: PropTypes.number,
  penSize: PropTypes.number,
  column: PropTypes.number,
  row: PropTypes.number,
  primaryColor: PropTypes.string,
  secondaryColor: PropTypes.string,
  image: PropTypes.any,
};

export default PixelPen;
