import {
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import { hexToRGBA } from '../../utils';

function AllPixelsSameColorAction({ onMouseUp, currentColor }) {
  const canvas = useContext(CanvasContext);
  const ctx = canvas.getContext('2d');
  const isMouseDown = useRef(false);
  const getImgLink = useCallback(() => {
    onMouseUp(canvas.toDataURL());
  }, [canvas, onMouseUp]);
  const setMouseDown = useCallback((e) => {
    isMouseDown.current = !isMouseDown.current;
    if (isMouseDown.current) {
      ctx.fillStyle = currentColor;
      const rect = canvas.getBoundingClientRect();

      const startX = Math.floor(e.clientX - rect.left);
      const startY = Math.floor(e.clientY - rect.top);

      const currentColorRGBA = hexToRGBA(currentColor);
      const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const pixelPos = (startY * canvas.width + startX) * 4;
      const red = imgData.data[pixelPos];
      const green = imgData.data[pixelPos + 1];
      const blue = imgData.data[pixelPos + 2];
      const alpha = imgData.data[pixelPos + 3];
      const startingColor = {
        r: red,
        g: green,
        b: blue,
        a: alpha,
      };
      for (let i = 0; i < imgData.data.length; i += 4) {
        if (
          imgData.data[i] === startingColor.r
          && imgData.data[i + 1] === startingColor.g
          && imgData.data[i + 2] === startingColor.b
          && imgData.data[i + 3] === startingColor.a
        ) {
          imgData.data[i] = currentColorRGBA.r;
          imgData.data[i + 1] = currentColorRGBA.g;
          imgData.data[i + 2] = currentColorRGBA.b;
          imgData.data[i + 3] = currentColorRGBA.a;
        }
      }
      ctx.putImageData(imgData, 0, 0);
    }
  }, [canvas, ctx, currentColor]);

  const unsetMouseDown = useCallback(() => {
    isMouseDown.current = !isMouseDown.current;
  }, []);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousedown', setMouseDown);
    canvas.addEventListener('mouseup', unsetMouseDown);
    canvas.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousedown', setMouseDown);
      canvas.removeEventListener('mouseup', unsetMouseDown);
      canvas.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, getImgLink, setMouseDown, unsetMouseDown]);

  return null;
}

AllPixelsSameColorAction.propTypes = {
  onMouseUp: PropTypes.func,
  currentColor: PropTypes.string,
};

export default AllPixelsSameColorAction;
