import React, { useCallback } from 'react';
import './frame.css';
import PropTypes from 'prop-types';

function Frame({
  active,
  checked,
  moving,
  onClick,
  onDelete,
  onDouble,
  onNumClick,
  num,
  image,
  onMouseDown,
}) {
  const handleDelete = (e) => {
    e.stopPropagation();
    onDelete(num, e);
  };
  const handleDouble = (e) => {
    e.stopPropagation();
    onDouble(num, e);
  };
  const handleNumClick = (e) => {
    e.stopPropagation();

    onNumClick(num, e);
  };

  const handleMouseDown = (e) => {
    onMouseDown(num, e);
  };

  const handleClick = useCallback(() => {
    onClick(num);
  }, [onClick, num]);

  return (
    <div
      className={`frame ${active ? 'frame--active' : ''} ${moving ? 'frame--moving' : ''}`}
      onMouseDown={handleMouseDown}
      onClick={handleClick}
    >
      {image && <img alt="pic" className="frame__img" src={image} />}

      <button
        onClick={handleDelete}
        onMouseDown={e => e.stopPropagation()}
        className="delete-action"
        title="Delete this frame"
      />
      <button
        onClick={handleDouble}
        onMouseDown={e => e.stopPropagation()}
        className="copy-action"
        title="Duplicate this frame"
      />
      <button
        onClick={handleNumClick}
        onMouseDown={e => e.stopPropagation()}
        className={`add-to-animation-action ${!checked ? 'add-to-animation-action--inactive' : ''}`}
        title="Toggle for preview"
      >
        {num + 1}
      </button>
    </div>
  );
}

Frame.propTypes = {
  active: PropTypes.bool,
  checked: PropTypes.bool,
  moving: PropTypes.bool,
  onClick: PropTypes.func,
  onDelete: PropTypes.func,
  onDouble: PropTypes.func,
  onMouseDown: PropTypes.func,
  onNumClick: PropTypes.func,
  onMouseUp: PropTypes.func,
  onMouseMove: PropTypes.func,
  num: PropTypes.number,
  image: PropTypes.any,
};

Frame.defaultProps = {
  active: false,
  checked: false,
  moving: false,
};

export default Frame;
