import React from 'react';
import './cursorPosition.css';
import PropTypes from 'prop-types';

function CursorPosition({ x, y, isOverCanvas }) {
  return (
    <div className={`cursor-position ${!isOverCanvas ? 'cursor-position--hidden' : ''}`}>
      {x} : {y}
    </div>
  );
}

CursorPosition.propTypes = {
  x: PropTypes.number,
  y: PropTypes.number,
  isOverCanvas: PropTypes.bool,
};

export default CursorPosition;
