import React, {
  useCallback,
  useState,
  useRef,
  useEffect,
} from 'react';
import PropTypes from 'prop-types';

import Frame from '../frame/Frame';
import './frameList.css';
import { clamp, getElemUnderCursor, getRelativeClientFrameY } from '../../utils';

function FrameList({
  activeFrame,
  checkedFrames,
  onDelete,
  onDouble,
  frames: propsFrames,
  onAddFrame,
  onChange,
  onFrameClick,
  onNumClick,
}) {
  const [frames, setFrames] = useState(propsFrames);
  const movingFrameNum = useRef(null);
  const frameClickRelativeY = useRef(null);
  const frameClone = useRef(null);
  const frameNumUnderCursor = useRef(-1);
  const handleDocumentMouseMove = useCallback((e) => {
    if (frameClickRelativeY.current === null || (e.movementY === 0 && e.movementX === 0)) {
      return;
    }

    if (!frameClone.current) {
      const targetClone = e.target.cloneNode(true);

      frameClone.current = targetClone;

      e.target.parentElement.appendChild(targetClone);
      targetClone.style.position = 'absolute';
      targetClone.style.zIndex = '10';
    }

    frameClone.current.style.top = `${e.clientY - frameClickRelativeY.current}px`;
    frameNumUnderCursor.current = getElemUnderCursor(e, frameClone.current);

    // Ignore moving clone over its position among frames
    if (frames[frameNumUnderCursor.current] && frames[frameNumUnderCursor.current].moving) {
      return;
    }

    frameNumUnderCursor.current = clamp(frameNumUnderCursor.current, 0, frames.length - 1);

    const newArr = frames.map(elem => ({
      ...elem,
      moving: false,
    }));

    newArr[frameNumUnderCursor.current] = {
      ...newArr[frameNumUnderCursor.current],
      moving: true,
    };

    setFrames(newArr);
  }, [frames]);

  useEffect(() => {
    document.addEventListener('mousemove', handleDocumentMouseMove);

    return () => document.removeEventListener('mousemove', handleDocumentMouseMove);
  }, [handleDocumentMouseMove]);

  const handleMouseDown = useCallback((num, e) => {
    frameClickRelativeY.current = getRelativeClientFrameY(e.target, e.clientY);
    movingFrameNum.current = num;
  }, []);

  const handleMouseUp = useCallback(() => {
    if (!frameClone.current) {
      frameClickRelativeY.current = null;
      movingFrameNum.current = null;

      return;
    }

    let nextFrames = [...frames];
    const movingFrame = nextFrames[movingFrameNum.current];
    const frameUnderCursor = nextFrames[frameNumUnderCursor.current];

    nextFrames[frameNumUnderCursor.current] = {
      ...movingFrame,
      moving: false,
    };
    nextFrames[movingFrameNum.current] = {
      ...frameUnderCursor,
      moving: false,
    };
    // Update indexes of all frames after changing positions
    nextFrames = nextFrames.map((elem, ind) => ({
      ...elem,
      num: ind,
    }));

    frameClone.current.parentElement.removeChild(frameClone.current);

    onChange(nextFrames);

    frameClickRelativeY.current = null;
    movingFrameNum.current = null;
    frameClone.current = null;
    frameNumUnderCursor.current = -1;
  }, [frames, onChange]);

  useEffect(() => {
    document.addEventListener('mouseup', handleMouseUp);

    return () => document.removeEventListener('mouseup', handleMouseUp);
  }, [handleMouseUp]);

  useEffect(() => {
    setFrames(propsFrames);
  }, [propsFrames]);

  return (
    <div className="frame-list-wrapper">
      <div className="frame-list">
        {frames.map(frame => (
          <Frame
            active={activeFrame === frame.num}
            checked={checkedFrames[frame.num]}
            key={frame.num}
            num={frame.num}
            moving={frame.moving}
            image={frame.image}
            onClick={onFrameClick}
            onNumClick={onNumClick}
            onDelete={onDelete}
            onDouble={onDouble}
            onMouseDown={handleMouseDown}
          />
        ))}
        <button className="add-frame-button" onClick={onAddFrame}>Add a new frame</button>
      </div>
    </div>
  );
}

FrameList.propTypes = {
  activeFrame: PropTypes.number,
  checkedFrames: PropTypes.object,
  frames: PropTypes.arrayOf(
    PropTypes.shape({
      num: PropTypes.number,
      image: PropTypes.any,
      moving: PropTypes.bool,
      key: PropTypes.number,
    }),
  ),
  onAddFrame: PropTypes.func,
  onChange: PropTypes.func,
  onDelete: PropTypes.func,
  onDouble: PropTypes.func,
  onFrameClick: PropTypes.func,
  onNumClick: PropTypes.func,
};

FrameList.defaultProps = {};

export default FrameList;
