import React, { useEffect, useCallback, useRef } from 'react';
import PropTypes from 'prop-types';
import './canvas.css';
import usePrev from '../../hooks/usePrev';
import { loadImage } from '../../utils';

export const CanvasContext = React.createContext(null);

function Canvas({
  active,
  children,
  className,
  clearImage,
  imgSrc,
  onChange,
  onMouseDown,
  onMouseMove,
  width,
  height,
  onMouseEnter,
  onMouseLeave,
  scale,
}) {
  const canvasRef = useRef(null);
  const prevScale = usePrev(scale);

  const clearCanvas = useCallback(
    () => canvasRef.current.getContext('2d').clearRect(0, 0, width, height),
    [height, width],
  );

  // eslint-disable-next-line consistent-return
  useEffect(() => {
    const onLoad = (e) => {
      clearCanvas();
      const img = e.target;
      const ctx = canvasRef.current.getContext('2d');

      if (clearImage) {
        clearCanvas();
      }

      if (scale !== prevScale) {
        const prevSmoothing = ctx.imageSmoothingEnabled;
        const dScale = scale / prevScale;

        clearCanvas();
        ctx.imageSmoothingEnabled = false;
        ctx.drawImage(img, 0, 0, img.width * dScale, img.height * dScale);
        ctx.imageSmoothingEnabled = prevSmoothing;
      } else {
        ctx.drawImage(img, 0, 0);
      }

      const dataUrl = canvasRef.current.toDataURL();

      // Prevent extra changes
      if (imgSrc !== dataUrl) {
        onChange(dataUrl);
      }
    };

    // If image changes, load it
    if (imgSrc) {
      const img = loadImage(imgSrc, onLoad);

      return () => img.removeEventListener('load', onLoad);
    }

    if (clearImage) {
      clearCanvas();
    }
  }, [clearCanvas, clearImage, height, imgSrc, onChange, prevScale, scale, width]);
  return (
    <canvas
      className={
        `canvas
        ${className}
        ${active.isEraseAction ? 'canvas__erase' : ''}
        ${active.isDrawAction ? 'canvas__draw' : ''}
        ${active.isChooseColorAction ? 'canvas__picker' : ''}
        ${active.isFillColorAction ? 'canvas__fill-color' : ''}
        ${active.isMoveAction ? 'canvas__move' : ''}
        `}
      ref={canvasRef}
      width={`${width}px`}
      height={`${height}px`}
      onMouseDown={onMouseDown}
      onMouseMove={onMouseMove}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      <CanvasContext.Provider value={canvasRef.current}>
        {children}
      </CanvasContext.Provider>
    </canvas>
  );
}

Canvas.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  clearImage: PropTypes.bool,
  imgSrc: PropTypes.string,
  height: PropTypes.number,
  name: PropTypes.string,
  width: PropTypes.number,
  onMouseDown: PropTypes.func,
  onMouseMove: PropTypes.func,
  onMouseEnter: PropTypes.func,
  onMouseLeave: PropTypes.func,
  onChange: PropTypes.func,
  scale: PropTypes.number,
  active: PropTypes.shape({
    isDrawAction: PropTypes.bool,
    isFillColorAction: PropTypes.bool,
    isEraseAction: PropTypes.bool,
    isChooseColorAction: PropTypes.bool,
    isMoveAction: PropTypes.bool,
  }),
};

export default Canvas;
