export const getRelativeClientFrameY = (elem, y) => {
  const targetRect = elem.getBoundingClientRect();
  const parentOffsetTop = targetRect.top - elem.offsetTop;
  const relativeY = y - targetRect.y;

  return relativeY + parentOffsetTop;
};

export const getRelativeClientX = (e) => {
  const targetRect = e.target.getBoundingClientRect();

  return e.clientX - targetRect.x;
};

export const getRelativeClientY = (elem, y) => {
  const targetRect = elem.getBoundingClientRect();

  return y - targetRect.y;
};

export const getElemUnderCursor = ((e, elem) => Math.round((e.clientY - getRelativeClientFrameY(elem, e.clientY)) / elem.clientHeight));

export const getRectSize = (width, scale) => Math.floor(width / scale);

export const getRectCoords = (x, y, canvasScale) => {
  const rectSize = getRectSize(640, canvasScale);
  const targetColumn = Math.floor(x / rectSize);
  const targetRow = Math.floor(y / rectSize);

  return { rectXBegin: targetColumn * rectSize, rectYBegin: targetRow * rectSize };
};

export const getCursorPosition = (x, y, canvasScale) => {
  const rectSize = getRectSize(640, canvasScale);
  const targetColumn = Math.floor(x / rectSize);
  const targetRow = Math.floor(y / rectSize);

  return { column: targetColumn, row: targetRow };
};

export const getRectCoordsFromEvent = (e, canvasScale) => {
  const targetRelativeClientY = getRelativeClientY(e.target, e.clientY);
  const targetRelativeClientX = getRelativeClientX(e);

  return getRectCoords(targetRelativeClientX, targetRelativeClientY, canvasScale);
};

export const hexToRGBA = (str) => {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(str);

  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16),
    a: 255,
  } : null;
};

// eslint-disable-next-line no-bitwise
export const rgbaToHEX = rgb => `#${(`000000${((rgb.r << 16) | (rgb.g << 8) | rgb.b).toString(16)}`).slice(-6)}`;

export const fillPixels = (startX, startY, canvasWidth, canvasHeight, colorLayer, startColor, paintColor) => {
  const pixelStack = [[startX, startY]];

  function matchStartColor(pixelPos) {
    const r = colorLayer.data[pixelPos];
    const g = colorLayer.data[pixelPos + 1];
    const b = colorLayer.data[pixelPos + 2];
    const a = colorLayer.data[pixelPos + 3];

    return (r === startColor.r && g === startColor.g && b === startColor.b && a === startColor.a);
  }

  function colorPixel(pixelPos) {
    // eslint-disable-next-line no-param-reassign
    colorLayer.data[pixelPos] = paintColor.r;
    // eslint-disable-next-line no-param-reassign
    colorLayer.data[pixelPos + 1] = paintColor.g;
    // eslint-disable-next-line no-param-reassign
    colorLayer.data[pixelPos + 2] = paintColor.b;
    // eslint-disable-next-line no-param-reassign
    colorLayer.data[pixelPos + 3] = paintColor.a;
  }

  while (pixelStack.length) {
    let reachLeft;
    let reachRight;
    const newPos = pixelStack.pop();
    const x = newPos[0];
    let y = newPos[1];

    let pixelPos = (y * canvasWidth + x) * 4;

    // eslint-disable-next-line no-plusplus
    while (y-- >= 0 && matchStartColor(pixelPos)) {
      pixelPos -= canvasWidth * 4;
    }

    pixelPos += canvasWidth * 4;
    y += 1;
    reachLeft = false;
    reachRight = false;
    // eslint-disable-next-line no-plusplus
    while (y++ < canvasHeight - 1 && matchStartColor(pixelPos)) {
      colorPixel(pixelPos);

      if (x > 0) {
        if (matchStartColor(pixelPos - 4)) {
          if (!reachLeft) {
            pixelStack.push([x - 1, y]);
            reachLeft = true;
          }
        } else if (reachLeft) {
          reachLeft = false;
        }
      }

      if (x < canvasWidth - 1) {
        if (matchStartColor(pixelPos + 4)) {
          if (!reachRight) {
            pixelStack.push([x + 1, y]);
            reachRight = true;
          }
        } else if (reachRight) {
          reachRight = false;
        }
      }

      pixelPos += canvasWidth * 4;
    }
  }
};

export const getRadius = (x1, y1, x2, y2) => {
  // Уравнение окружности ω (A; R) имеет вид (x – a)2 + (y – b)2 = R2,
  // где a и b – координаты центра A окружности ω (A; R) .
  let centerX = x1 + Math.floor((x2 - x1) / 2);
  let centerY = y1 + Math.floor((y2 - y1) / 2);
  if (x1 === x2) {
    centerX = x1;
  }

  if (y1 === y2) {
    centerY = y1;
  }

  return Math.floor(Math.sqrt((x2 - centerX) * (x2 - centerX) + (y2 - centerY) * (y2 - centerY)));
};

export const getCircleCenter = (x1, y1, x2, y2) => {
  let centerX = x1 + Math.floor((x2 - x1) / 2);
  let centerY = y1 + Math.floor((y2 - y1) / 2);

  if (x1 === x2) {
    centerX = x1;
  }

  if (y1 === y2) {
    centerY = y1;
  }

  return { x: centerX, y: centerY };
};

export const getHorizontalRadius = (x1, x2) => Math.floor(Math.abs(x1 - x2) / 2);

export const getVerticalRadius = (y1, y2) => Math.floor(Math.abs(y1 - y2) / 2);

export const clamp = (value, min, max) => Math.min(Math.max(value, min), max);

export const loadImage = (src, onLoad) => {
  const img = new Image();

  img.src = src;
  img.addEventListener('load', onLoad);

  return img;
};

export const loadImages = (sources) => {
  const loadImageAsync = src => new Promise(resolve => loadImage(src, e => resolve(e.target)));

  return Promise.all(sources.map(loadImageAsync));
};

export const even = val => val % 2 === 0;

export const odd = val => !even(val);

export const download = (url, title) => {
  const link = document.createElement('a');

  link.setAttribute('href', url);
  link.setAttribute('download', title);
  link.click();
};
