import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import './preview.css';

function Preview({
  frames, framesPerSecond, onFullScreenShow,
}) {
  const [frameInd, setFrameInd] = useState(0);
  const currentFrame = frames[frameInd];
  const previewRef = useRef(null);

  useEffect(() => {
    const timer = setTimeout(() => {
      const nextFrame = frameInd < frames.length - 1
        ? frameInd + 1
        : 0;

      setFrameInd(nextFrame);
    }, 1000 / framesPerSecond);

    return () => clearTimeout(timer);
  }, [framesPerSecond, frameInd, frames]);
  const getTooltip = (() => <span className="tooltip">Full screen preview</span>);

  return (
    <div className="fullscreen-preview" ref={previewRef}>
      <div className="preview">
        {currentFrame && currentFrame.image
          ? <img className="frame__img-active" src={currentFrame.image} alt="Frame" />
          : <div className="emptyFrame" />
        }
        <button
          onClick={ e => onFullScreenShow(previewRef.current, e)}
          onMouseOver={getTooltip}
          className="full-screen-action"
          title="Full screen preview"
        />
      </div>
    </div>
  );
}

Preview.propTypes = {
  frames: PropTypes.arrayOf(
    PropTypes.shape({
      num: PropTypes.number,
      image: PropTypes.any,
    }),
  ),
  framesPerSecond: PropTypes.number,
  onFullScreenShow: PropTypes.func,
  onExportButton: PropTypes.func,
};

export default Preview;
