import {
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import { getRectCoords } from '../../utils';

function DarkenColor({ onMouseUp, canvasScale, penSize }) {
  const canvas = useContext(CanvasContext);
  const ctx = canvas.getContext('2d');
  const rect = canvas.getBoundingClientRect();
  const isMouseDown = useRef(false);
  const setMouseDown = useCallback(() => {
    isMouseDown.current = !isMouseDown.current;
  }, []);
  const mouseUpHandler = useCallback(() => {
    onMouseUp(canvas.toDataURL());
    isMouseDown.current = !isMouseDown.current;
  }, [canvas, onMouseUp]);

  const mouseMoveHandler = useCallback((e) => {
    if (isMouseDown.current) {
      const currentX = Math.floor(e.clientX - rect.left);
      const currentY = Math.floor(e.clientY - rect.top);
      const rectCoords = getRectCoords(currentX, currentY, canvasScale);
      const imgData = ctx.getImageData(rectCoords.rectXBegin, rectCoords.rectYBegin, penSize, penSize);

      for (let i = 0; i < imgData.data.length; i += 4) {
        if (imgData.data[i + 3] !== 0) {
          if (imgData.data[i] >= 5) {
            imgData.data[i] -= 5;
          }
          if (imgData.data[i + 1] >= 5) {
            imgData.data[i + 1] -= 5;
          }
          if (imgData.data[i + 2] >= 5) {
            imgData.data[i + 2] -= 5;
          }
        }
      }
      ctx.putImageData(imgData, rectCoords.rectXBegin, rectCoords.rectYBegin);
    }
  }, [canvasScale, ctx, penSize, rect.left, rect.top]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }
    canvas.addEventListener('mousedown', setMouseDown);
    canvas.addEventListener('mousemove', mouseMoveHandler);
    document.addEventListener('mouseup', mouseUpHandler);
    return () => {
      canvas.removeEventListener('mousedown', setMouseDown);
      canvas.removeEventListener('mousemove', mouseMoveHandler);
      document.removeEventListener('mouseup', mouseUpHandler);
    };
  }, [canvas, mouseMoveHandler, mouseUpHandler, setMouseDown]);
  return null;
}

DarkenColor.propTypes = {
  onMouseUp: PropTypes.func,
};

export default DarkenColor;
