import {
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';

function Pen({ onMouseUp, ...props }) {
  const canvas = useContext(CanvasContext);
  const isMouseDown = useRef(false);
  const getImgLink = useCallback(() => {
    if (isMouseDown.current) {
      onMouseUp(canvas.toDataURL());

      isMouseDown.current = !isMouseDown.current;
    }
  }, [onMouseUp, canvas]);

  const handler = useCallback((e) => {
    const ctx = canvas.getContext('2d');
    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    if (isMouseDown.current) {
      ctx.lineWidth = props.penSize / 8;
      ctx.lineTo(x, y);
      ctx.strokeStyle = props.currentColor;
      ctx.stroke();
      ctx.beginPath();
      ctx.arc(x, y, props.penSize / 16, 0, Math.PI * 2);
      ctx.fillStyle = props.currentColor;
      ctx.fill();
      ctx.beginPath();
      ctx.moveTo(x, y);
    } else {
      ctx.beginPath();
    }
  }, [canvas, props.penSize, props.currentColor]);

  const setMouseDown = useCallback((e) => {
    isMouseDown.current = !isMouseDown.current;
    handler(e);
  }, [handler]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', setMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', setMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, setMouseDown]);

  return null;
}

Pen.propTypes = {
  penSize: PropTypes.number,
  onMouseDown: PropTypes.func,
  currentColor: PropTypes.string,
};

export default Pen;
