import {
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';

function VerticalMirrorPen({ onMouseUp, ...props }) {
  const canvas = useContext(CanvasContext);
  const isMouseDown = useRef(false);
  const getImgLink = useCallback(() => {
    onMouseUp(canvas.toDataURL());
  }, [onMouseUp, canvas]);

  const handler = useCallback((e) => {
    const ctx = canvas.getContext('2d');

    if (isMouseDown.current) {
      if (e.shiftKey) {
        const y = e.target.offsetHeight - props.y - props.height;

        ctx.fillStyle = props.currentColor;
        ctx.strokeStyle = props.currentColor;
        ctx.fillRect(props.x, props.y, props.width, props.height);
        ctx.fillRect(props.x, y, props.width, props.height);
      } else {
        const x = e.target.offsetWidth - props.x - props.width;
        ctx.fillStyle = props.currentColor;
        ctx.strokeStyle = props.currentColor;
        ctx.fillRect(props.x, props.y, props.width, props.height);
        ctx.fillRect(x, props.y, props.width, props.height);
      }
    } else {
      ctx.beginPath();
    }
  }, [props.x, props.y, props.width, props.height, props.currentColor, isMouseDown, canvas]);

  const setMouseDown = useCallback((e) => {
    isMouseDown.current = !isMouseDown.current;
    handler(e);
  }, [handler]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', setMouseDown);
    document.addEventListener('mouseup', setMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', setMouseDown);
      document.removeEventListener('mouseup', setMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, setMouseDown]);

  return null;
}

VerticalMirrorPen.propTypes = {
  x: PropTypes.number,
  y: PropTypes.number,
  width: PropTypes.number,
  height: PropTypes.number,
  onMouseDown: PropTypes.func,
  currentColor: PropTypes.string,
};

export default VerticalMirrorPen;
