import { useContext, useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import { rgbaToHEX } from '../../utils';

function ChooseColor({ onMouseUp }) {
  const canvas = useContext(CanvasContext);
  const ctx = canvas.getContext('2d');
  const getColorValue = useCallback((e) => {
    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;
    const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixelPos = (y * canvas.width + x) * 4;
    const red = imgData.data[pixelPos];
    const green = imgData.data[pixelPos + 1];
    const blue = imgData.data[pixelPos + 2];
    const alpha = imgData.data[pixelPos + 3];
    const rgba = {
      r: red,
      g: green,
      b: blue,
      a: alpha,
    };
    const chosenColor = rgbaToHEX(rgba);
    onMouseUp(chosenColor);
  }, [canvas, ctx, onMouseUp]);
  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }
    canvas.addEventListener('mouseup', getColorValue);
    return () => {
      canvas.removeEventListener('mouseup', getColorValue);
    };
  }, [canvas, getColorValue]);
  return null;
}

ChooseColor.propTypes = {
  onMouseUp: PropTypes.func,
};

export default ChooseColor;
