import React from 'react';
import PropTypes from 'prop-types';

import './penSize.css';

function PenSize({
  active,
  onPenSizeAction,
}) {
  return (
      <div className="pen-size" onClick={onPenSizeAction} title="Pen size from 1 to 4 pixels">
        <div className={`pen-size__item pen-size__itemS ${active.isPenSizeAction1 ? 'pen-size__item--active' : ''} `}>
          <div data-size='1' />
        </div>
        <div className={`pen-size__item pen-size__itemM ${active.isPenSizeAction2 ? 'pen-size__item--active' : ''} `}>
          <div data-size='2' />
        </div>
        <div className={`pen-size__item pen-size__itemL ${active.isPenSizeAction3 ? 'pen-size__item--active' : ''} `}>
          <div data-size='3' />
        </div>
        <div className={`pen-size__item pen-size__itemXL ${active.isPenSizeAction4 ? 'pen-size__item--active' : ''} `}>
          <div data-size='4' />
        </div>
      </div>
  );
}

PenSize.propTypes = {
  active: PropTypes.shape({
    isPenSizeAction1: PropTypes.bool,
    isPenSizeAction2: PropTypes.bool,
    isPenSizeAction3: PropTypes.bool,
    isPenSizeAction4: PropTypes.bool,
  }),
  onPenSizeAction: PropTypes.func,
};

PenSize.defaultProps = {
  active: false,
};

export default PenSize;
