import {
  useContext,
  useEffect,
  useCallback,
  useRef,
  useState,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import {
  getRectCoords,
  getRectCoordsFromEvent,
  hexToRGBA,
  getCircleCenter,
  getHorizontalRadius,
  getVerticalRadius,
  getRadius,
} from '../../utils';
import drawMidPointEllipse from '../../algorithms/drawMidPointEllipse';
import drawingCircle from '../../algorithms/drawingCircle';

function DrawCircle({ onMouseUp, ...props }) {
  const [circleEndX, setCircleEndX] = useState(0);
  const [circleEndY, setCircleEndY] = useState(0);
  const canvas = useContext(CanvasContext);
  const isMouseDown = useRef(false);
  const imageData = useRef(null);
  const getImgLink = useCallback(() => {
    imageData.current = null;
    isMouseDown.current = false;
    onMouseUp(canvas.toDataURL());
  }, [onMouseUp, canvas]);
  const ctx = canvas.getContext('2d');
  const handler = useCallback((e) => {
    if (isMouseDown.current) {
      requestAnimationFrame(() => {
        setCircleEndX(getRectCoordsFromEvent(e, props.canvasScale).rectXBegin);
        setCircleEndY(getRectCoordsFromEvent(e, props.canvasScale).rectYBegin);

        if (imageData.current) {
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          ctx.putImageData(imageData.current, 0, 0);
        }

        const setPixel = (x, y, currentColorRgba) => {
          ctx.fillStyle = props.currentColor;
          const { rectXBegin, rectYBegin } = getRectCoords(x, y, props.canvasScale);
          const { data } = ctx.getImageData(rectXBegin, rectYBegin, 1, 1);

          if (
            data[0] === currentColorRgba.r
            && data[1] === currentColorRgba.g
            && data[2] === currentColorRgba.b
            && data[3] === currentColorRgba.a
          ) {
            return;
          }

          ctx.fillRect(rectXBegin, rectYBegin, props.rectSize, props.rectSize);
        };

        const plot = (x, y, rgba) => {
          // кисточка - ставит пиксель своего цвета
          // eslint-disable-next-line no-restricted-globals
          if (isFinite(x) && isFinite(y)) {
            setPixel(x, y, rgba);
          }
        };

        ctx.strokeStyle = props.currentColor;
        ctx.fillStyle = props.currentColor;
        const x1 = props.x;
        const y1 = props.y;
        const x2 = circleEndX;
        const y2 = circleEndY;
        const rgba = hexToRGBA(props.currentColor);
        const radius = getRadius(x1, y1, x2, y2);
        const circleCenter = getCircleCenter(x1, y1, x2, y2);
        const horizontalRadius = getHorizontalRadius(x1, x2);
        const verticalRadius = getVerticalRadius(y1, y2);

        if (e.shiftKey) {
          drawingCircle(x2, y2, radius, (x, y) => {
            plot(x, y, rgba);
          });
        } else {
          drawMidPointEllipse(horizontalRadius, verticalRadius, circleCenter.x, circleCenter.y, (p1, p2, p3, p4) => {
            plot(p1[0], p1[1], rgba);
            plot(p2[0], p2[1], rgba);
            plot(p3[0], p3[1], rgba);
            plot(p4[0], p4[1], rgba);
          });
        }
      });
    } else {
      ctx.beginPath();
    }
  }, [
    props.canvasScale,
    props.currentColor,
    props.x,
    props.y,
    props.rectSize,
    ctx,
    canvas.width,
    canvas.height,
    circleEndX,
    circleEndY,
  ]);

  const toggleMouseDown = useCallback((e) => {
    isMouseDown.current = !isMouseDown.current;
    imageData.current = ctx.getImageData(0, 0, canvas.width, canvas.height);

    setCircleEndX(getRectCoordsFromEvent(e, props.canvasScale).rectXBegin);
    setCircleEndY(getRectCoordsFromEvent(e, props.canvasScale).rectYBegin);
  }, [canvas.height, canvas.width, ctx, props.canvasScale]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', toggleMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', toggleMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, toggleMouseDown]);

  return null;
}

DrawCircle.propTypes = {
  onMouseDown: PropTypes.func,
  currentColor: PropTypes.string,
  canvasScale: PropTypes.number,
  x: PropTypes.number,
  y: PropTypes.number,
  rectSize: PropTypes.number,
};

export default DrawCircle;
