import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import './export.css';

function Export({
  onPNGExport,
  onPNGSetExport,
  onGIFExport,
  onResetClick,
  onAPNGExport,
  onSave,
}) {
  const isMouseDown = useRef(false);

  const onExportMenu = () => {
    isMouseDown.current = !isMouseDown.current;
    if (isMouseDown.current) {
      document.querySelector('.result-export__list').classList.add('result-export__list--active');
      document.querySelector('.result-export__button').classList.add('result-export__button--active');
    } else {
      document.querySelector('.result-export__list').classList.remove('result-export__list--active');
      document.querySelector('.result-export__button').classList.remove('result-export__button--active');
    }
  };
  return (
    <div className="result-export__wrapper">
      <button className="result-export__button" title="Export" onClick={onExportMenu} />
      <div className="result-export__list">
        <button
          className="result-export__item png"
          onClick={onPNGExport}
          title="Download active frame as a PNG file"
        />
        <button
          className="result-export__item set-png"
          onClick={onPNGSetExport}
          title="Download all frames as PNG files"
        />
        <button
          className="result-export__item gif"
          onClick={onGIFExport}
          title="Download as animated GIF"
        />
        <button
          className="result-export__item apng"
          title="Download as animated APNG"
          onClick={onAPNGExport}
        />
        <button
          className="result-export__item save"
          title="Save as .piskel"
          onClick={onSave}
        />
        <button
          className="result-export__item reset"
          title="Reset all saves"
          onClick={onResetClick}
        />
      </div>
    </div>
  );
}

Export.propTypes = {
  onPNGExport: PropTypes.func,
  onPNGSetExport: PropTypes.func,
  onGIFExport: PropTypes.func,
  onResetClick: PropTypes.func,
  onAPNGExport: PropTypes.func,
  onSave: PropTypes.func,
};

export default Export;
