import {
  useContext,
  useEffect,
  useCallback,
  useRef,
  useState,
} from 'react';
import PropTypes from 'prop-types';
import { CanvasContext } from '../canvas/Canvas';
import {
  getRectCoordsFromEvent,
} from '../../utils';

function DrawFillRectangle({ onMouseUp, ...props }) {
  const [rectEndX, setRectEndX] = useState(0);
  const [rectEndY, setRectEndY] = useState(0);
  const canvas = useContext(CanvasContext);
  const isMouseDown = useRef(false);
  const imageData = useRef(null);
  const getImgLink = useCallback(() => {
    imageData.current = null;
    isMouseDown.current = false;
    onMouseUp(canvas.toDataURL());
  }, [onMouseUp, canvas]);
  const ctx = canvas.getContext('2d');

  const handler = useCallback((e) => {
    if (isMouseDown.current) {
      requestAnimationFrame(() => {
        setRectEndX(getRectCoordsFromEvent(e, props.canvasScale).rectXBegin);
        setRectEndY(getRectCoordsFromEvent(e, props.canvasScale).rectYBegin);

        if (imageData.current) {
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          ctx.putImageData(imageData.current, 0, 0);
        }

        ctx.fillStyle = props.primaryColor;
        const x1 = props.x;
        const y1 = props.y;
        const x2 = rectEndX;
        const y2 = rectEndY;
        const outerRectWidth = Math.abs(x1 - x2);
        const outerRectHeight = Math.abs(y1 - y2);
        // if x1 > x2
        const horizontalSide1 = Math.abs((x1 - props.rectSize) - (x2 + props.rectSize));
        // if x1 < x2
        const horizontalSide2 = Math.abs((x2 - props.rectSize) - (x1 + props.rectSize));
        // if y1 > y2
        const verticalSide1 = Math.abs((y1 - props.rectSize) - (y2 + props.rectSize));
        // if y1 < y2
        const verticalSide2 = Math.abs((y2 - props.rectSize) - (y1 + props.rectSize));
        const innerRectWidth = Math.min(horizontalSide1, horizontalSide2);
        const innerRectHeight = Math.min(verticalSide1, verticalSide2);

        ctx.fillRect(Math.min(x1, x2), Math.min(y1, y2), outerRectWidth, outerRectHeight);
        if (outerRectWidth >= props.rectSize * 3 && outerRectHeight >= props.rectSize * 3) {
          ctx.fillStyle = props.secondaryColor;
          const innerSide1 = Math.min(x1 + props.rectSize, x2 + props.rectSize);
          const innerSide2 = Math.min(y1 + props.rectSize, y2 + props.rectSize);
          ctx.fillRect(innerSide1, innerSide2, innerRectWidth, innerRectHeight);
        }
      });
    } else {
      ctx.beginPath();
    }
  }, [props.canvasScale,
    props.primaryColor,
    props.x,
    props.y,
    props.rectSize,
    props.secondaryColor,
    ctx,
    rectEndX,
    rectEndY,
    canvas.width,
    canvas.height]);

  const toggleMouseDown = useCallback((e) => {
    isMouseDown.current = true;
    imageData.current = ctx.getImageData(0, 0, canvas.width, canvas.height);

    setRectEndX(getRectCoordsFromEvent(e, props.canvasScale).rectXBegin);
    setRectEndY(getRectCoordsFromEvent(e, props.canvasScale).rectYBegin);
  }, [canvas.height, canvas.width, ctx, props.canvasScale]);

  useEffect(() => {
    if (!canvas) {
      return Function.prototype;
    }

    canvas.addEventListener('mousemove', handler);
    canvas.addEventListener('mousedown', toggleMouseDown);
    document.addEventListener('mouseup', getImgLink);

    return () => {
      canvas.removeEventListener('mousemove', handler);
      canvas.removeEventListener('mousedown', toggleMouseDown);
      document.removeEventListener('mouseup', getImgLink);
    };
  }, [canvas, handler, getImgLink, toggleMouseDown]);

  return null;
}

DrawFillRectangle.propTypes = {
  onMouseDown: PropTypes.func,
  primaryColor: PropTypes.string,
  secondaryColor: PropTypes.string,
  canvasScale: PropTypes.number,
  x: PropTypes.number,
  y: PropTypes.number,
  rectSize: PropTypes.number,
};

export default DrawFillRectangle;
